import io from 'socket.io-client'
let webSocket: any

export const createWebsocket = () => {
  // http://192.168.161.175/
  // 192.168.1.101
  webSocket = io('http://localhost:8081', {
    query: {
      username: localStorage.getItem('username'),
    },
  })
}
export const getWebSocket = () => {
  if (webSocket === null) {
    webSocket = io('http://localhost:8081', {
      query: {
        username: localStorage.getItem('username'),
      },
    })
  }
  return webSocket
}
